import * as fromSideNavLayout from '@core/reducers/sideNav-reducers';
import { createFeatureSelector, ActionReducerMap, createSelector } from '@ngrx/store';

export interface AppState {
  SIDENAV: fromSideNavLayout.State;
}

export const reducers: ActionReducerMap<AppState> = {
  SIDENAV: fromSideNavLayout.reducer,
};

export const getProfileSate = createFeatureSelector<string>('PROFILE_IMAGE');
export const getSideNavState = createFeatureSelector<fromSideNavLayout.State>('SIDENAV');

export const getCurrentUnpinnedItem = createSelector(getSideNavState, (state) => state.currentUnPinnedItem);
export const getSideNavCollapsedState = createSelector(getSideNavState, (state) => state.collapsed);
export const getPinnedItemsState = createSelector(getSideNavState, (state) => state.pinnedItems);
