import { LayoutActionTypes, LayoutActions } from '../actions/layout-actions';
import { IPinnedSindeNavItem } from '@core/models/pinned-sidenav-item';

export interface State {
  collapsed: boolean;
  pinnedItems: Array<IPinnedSindeNavItem>;
  currentUnPinnedItem: number;
}

const initialState: State = {
  collapsed: true,
  pinnedItems: [],
  currentUnPinnedItem: null
};

export function reducer(state = initialState, action: LayoutActions) {
  const clonedState = Object.assign({}, state);
  switch (action.type) {
    case LayoutActionTypes.ToggleSideNav: {
      clonedState.collapsed = action.payload;
      return clonedState;
    }
    case LayoutActionTypes.PinnedItems: {
      clonedState.pinnedItems = action.payload;
      return clonedState;
    }
    case LayoutActionTypes.CurrentUnPinnedItem: {
      clonedState.currentUnPinnedItem = action.payload;
      return clonedState;
    }
    default:
      return state;
  }
}
