import { Injectable, Inject } from '@angular/core';
import { ApiService } from '@core/services/api.service';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';

@Injectable({
  providedIn: 'root'
})
export class LabApiService {
  readonly MODULE_NAME = 'LAB';

  constructor(
    @Inject(URL_CONSTANTS_TOKEN) private urlConstants: UrlConstants,
    private apiService: ApiService
  ) {}

  insertOrUpdateDataForTemplate<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.LAB.INSERT_OR_UPDATE_DATA_FOR_TEMPLATE,
      payload
    );
  }
  updateImageTestResult<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.LAB.UPDATE_IMAGE_TEST_RESULT,
      payload
    );
  }

  fetchDynamicTemplateData<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.LAB.FETCH_DYMANIC_TEMPLATE_DATA,
      payload
    );
  }

  servicesListForLabAndImaging<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.LAB.FETCH_AMOUNT_DEFINED_TEST_SERVICES_FOR_LAB,
      payload
    );
  }
}
