import { Component, OnInit, ViewChild, ElementRef, AfterViewInit, Renderer2 } from '@angular/core';
import { UserService } from '@core/services/user.service';
import { Observable } from 'rxjs';
import { UserSession } from '@core/models/user-session';
import { Store, select } from '@ngrx/store';
import { ToggleSideNav, CurrentUnPinnedItem } from '@core/actions/layout-actions';
import { getPinnedItemsState, getSideNavCollapsedState, AppState } from '@app/reducers';
import { Router } from '@angular/router';
import { LoginApiService } from '@core/services';
import { IPinnedSindeNavItem } from '@core/models/pinned-sidenav-item';

@Component({
  templateUrl: './top-nav.component.html',
  styleUrls: ['./top-nav.component.scss'],
  selector: 'app-top-nav'
})
export class TopNavComponent implements OnInit, AfterViewInit {

  public pinnedScreens;
  public sideNavCollapsed;
  public pinnedItemsState$: Observable<Array<IPinnedSindeNavItem>>;
  public sideNavToggleState$: Observable<boolean>;
  profileImageLocation: string;
  userData$: Observable<UserSession>;
  userSession: UserSession;

  @ViewChild('pinnedItemsContainer')
  pinnedItemsContainer: ElementRef;

  constructor(
    private loginService: LoginApiService,
    private router: Router,
    private renderer: Renderer2,
    private store: Store<AppState>,
    private userService: UserService
  ) {
    this.pinnedItemsState$ = this.store.pipe(select(getPinnedItemsState));
    this.sideNavToggleState$ = this.store.pipe(select(getSideNavCollapsedState));
    this.setUserProfileSubscription();
  }

  ngAfterViewInit() {
    this.renderer.listen(this.pinnedItemsContainer.nativeElement, 'wheel', (event: WheelEvent) => {
      const targetElement: Element = this.pinnedItemsContainer.nativeElement;
      targetElement.scrollLeft -= event.deltaY * 20;
      event.preventDefault();
    });
  }

  ngOnInit() {
    this.pinnedItemsState$.subscribe((pinnedItems) => {
      this.pinnedScreens = pinnedItems;
    });
    this.sideNavToggleState$.subscribe((toggleState) => {
      this.sideNavCollapsed = toggleState;
    });
  }

  /**
   * Subscribes to [userSource$]{@link UserService#userSource},
   * which emits user session session information whever new user session information is available
   */
  setUserProfileSubscription() {
    this.userService.userSource$.subscribe(userSession => {
      this.userSession = userSession;
      if (userSession) {
        if (userSession.imagePath) {
          this.profileImageLocation = encodeURI(userSession.imagePath);
        } else if (userSession.doctorObj && userSession.doctorObj.doc_img) {
          this.profileImageLocation = encodeURI(userSession.doctorObj.doc_img);
        }
      }
    });
  }

  openScreen(item) {
    this.router.navigateByUrl(item.screenLink);
  }

  togglesideMenu() {
    this.store.dispatch(new ToggleSideNav(!this.sideNavCollapsed));
  }

  unHeart(unheartedScreen) {
    const payload = {
      orgId: this.userSession.orgId,
      createdUsrId: this.userSession.regId,
      empId: this.userSession.regId,
      orgGroupId: this.userSession.orgGroupId,
      insert: false,
      screenId: unheartedScreen.screenId
    };
    this.loginService.insertOrUpdateEmployeeFavouriteScrean(payload)
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          const unheartedScreenIndex = this.pinnedScreens
            .findIndex((screen) => unheartedScreen.screenId === screen.screenId);
          if (unheartedScreenIndex > -1) {
            this.pinnedScreens.splice(unheartedScreenIndex, 1);
            this.store.dispatch(new CurrentUnPinnedItem(unheartedScreen.screenId));
          }
        }
      });
  }
}
