import { Injectable, Inject } from '@angular/core';
import { MOMENT } from '@core/services/lib.providers';
import { UserSession } from '@core/models/user-session';

@Injectable({
  providedIn: 'root'
})
export class UtilService {

  constructor(@Inject(MOMENT) private moment) {}

  /**
   * Divides an array into chunks of equal size
   * @param {Array<any>} arr The array to be chunked into
   * @param {number} chunk_size Size of the chunk
   * @returns {Array<Array<any>>} chunked array
   * ```ts
   * // returns [[10, 30], [40, 50]]
   * arrayChunker([10, 30, 40, 50], 2);
   * ```
   */
  arrayChunker(arr: Array<any>, chunk_size: number): Array<Array<any>> {
    return arr
      .map((e, i) =>
        i % chunk_size === 0 ? arr.slice(i, i + chunk_size) : null
      )
      .filter(e => e);
  }

  // checkLocalData() {
  //   console.log(this.data);
  //   return JSON.parse(atob(this.data));
  // }

  /**
   * Generates a unique string
   * @returns {string} unique string
   * ```ts
   * // generates a random string of kind
   * // gq1f4gl77g-yyg6yomwitm-30lcmd7b4tm-2tfovta1tq4-qzyeac1ane
   * generateUniqueId()
   *
   * ```
   */
  generateUniqueId() {
    return Array.from(new Array(5))
      .map(() => `${this.getRandomString()}`)
      .join('-');
  }

  /**
   * @description
   * converts Year/Month/Days to days
   * @param {string} years User age Year like 25/30/55 etc..,
   * @returns days like for 25 years it will return 9131 days
   */
  getAgeInDays(years, months, days) {
    if (years || months || days) {
      const ageYears = years || 0,
        ageMonths = months || 0,
        ageDays = days || 0;
      // const then = this.moment().subtract(ageYears, 'years').subtract(ageMonths, 'months').subtract(ageDays, 'days'),
      //   now = this.moment();

      const ageInDays =
        parseFloat(ageYears) * 365 +
        parseFloat(ageMonths) * 31 +
        parseInt(ageDays, 10);
      return ageInDays;
    }
  }

  /**
   * Processes the age given in the international format and returns an map containing years, months and days
   * @param {string} dobInString Person date of birth in the international format; for example 1995-03-26
   * @returns {Map<string, number>} A map containing years, months and days
   * ```ts
   * // returns Map(3) {"years" => 23, "months" => 3, "days" => 2}
   * parseDob('1995-03-26')
   * ```
   */
  parseDob(dobInString: string): Map<string, number> {
    let patientAge;

    const birthDate = new Date(dobInString);
    const today = new Date();

    const y = [today.getFullYear(), birthDate.getFullYear()];
    let ydiff = y[0] - y[1];

    const m = [today.getMonth(), birthDate.getMonth()];
    let mdiff = m[0] - m[1];

    const d = [today.getDate(), birthDate.getDate()];
    let ddiff = d[0] - d[1];

    if (mdiff < 0 || (mdiff === 0 && ddiff < 0)) {
      --ydiff;
    }

    if (mdiff < 0 || (mdiff === 0 && ddiff < 0)) {
      mdiff += 12;
    }

    if (ddiff < 0) {
      birthDate.setMonth(m[1] + 1, 0);
      ddiff = birthDate.getDate() - d[1] + d[0];
      --mdiff;
    }
    if (mdiff) {
      patientAge = `${mdiff} Months `;
    }
    if (!ydiff && !mdiff) {
      patientAge = `${ddiff} Days`;
    }
    if (!ydiff && !mdiff && !ddiff) {
      patientAge = `${ydiff} Years`;
    }
    if (ydiff) {
      patientAge = `${ydiff} Years`;
    }

    return new Map()
      .set('years', ydiff)
      .set('months', mdiff)
      .set('days', ddiff)
      .set('patientAge', patientAge);
  }

  /**
   * Processes the age given in days and returns an map containing years, months and days
   * @param {number} ageInDays Person's age in days
   * @returns {Map<string, number>} A map containing years, months and days
   * ```ts
   * // returns Map(3) {"years" => 24, "months" => 7, "days" => 22}
   * parseAgeIndays(9000);
   * ```
   */
  parseAgeIndays(ageInDays: number): Map<string, number> {
    let patientAge;
    let ageDaysText = 'Days';
    let ageMonthsText = 'Months';
    let ageYearsText = 'Years';
    const patientAgeYear = Math.floor(ageInDays / 365);
    const patientAgeMonth = Math.floor((ageInDays % 365) / 31);
    const patientAgeDays =
      ageInDays - patientAgeYear * 365 - patientAgeMonth * 31;
      if (patientAgeMonth) {
        if (patientAgeMonth === 1) {
          ageMonthsText = 'Month';
        }
        patientAge = `${patientAgeMonth} ${ageMonthsText}`;
      }
    if (!patientAgeYear && !patientAgeMonth) {
      if (patientAgeDays === 1) {
        ageDaysText = 'Day';
      }
      patientAge = `${patientAgeDays} ${ageDaysText}`;
    }
    if (!patientAgeYear && !patientAgeMonth && !patientAgeDays) {
        ageYearsText = 'Years';
      patientAge = `${patientAgeYear} ${ageYearsText}`;
    }
    if (patientAgeYear) {
      if (patientAgeYear === 1 ) {
        ageYearsText = 'Year';
      }
      patientAge = `${patientAgeYear} ${ageYearsText}`;
    }
    return new Map()
      .set('years', patientAgeYear)
      .set('months', patientAgeMonth)
      .set('days', patientAgeDays)
      .set('patientAge', patientAge);
  }

  /**
   * @description converts the output of parseDob to number of days
   * @returns {number} age in days
   * @param {number} years years atom
   * @param {number} months months atom
   * @param {number} days days atom
   */
  parsedDobToAgeInDays(years: number, months: number, days: number) {
    const then = this.moment()
      .subtract(years, 'years')
      .subtract(months, 'months')
      .subtract(days, 'days');
    const now = this.moment();
    return now.diff(then, 'days');
  }

  /**
   * @param {Map} map A map to convert to an object
   * Converts an map to an object
   * @returns An object which has been constructed using map
   * ```ts
   * // returns { years: 23, months: 3, days: 2 }
   * const map = new Map([['years', 23],['months', 3],['days', 2]])
   * mapToObject(map);
   * ```
   */
  mapToObject(map: Map<string, number>) {
    const obj = {};
    map.forEach((val, key) => {
      obj[key] = val;
    });
    return obj;
  }

  /**
   * This method convers a query string into an hash of query params
   * @param {string} queryString Query string
   * @returns {Object} A hash of query params
   * ```ts
   * // returns {a: "10", b: "40"}
   * parseQueryString('?a=10&b=40')
   * ```
   */
  parseQueryString(queryString: string) {
    const urlParams = {};
    queryString.replace(
      new RegExp('([^?=&]+)(=([^&]*))?', 'g'),
      ($0, $1, $2, $3) => (urlParams[$1] = $3)
    );
    return urlParams;
  }

  /**
   * @async
   * Reads a file an returns its content as an array buffer
   * @param file A file to read as an array buffer
   * @return {Promise<ArrayBuffer>} A promise that returns an array buffer if resolved, or an Error if rejected
   * ```ts
   * readFileAsArrayBuffer(fileObject)
   *  .then((data: ArrayBuffer) => {
   *    // consume data here
   *  })
   *  .catch((err: Error) => {
   *    // error occurred
   *  });
   * ```
   */
  readFileAsArrayBuffer(file: File): Promise<ArrayBuffer> {
    const reader = new FileReader();
    reader.readAsArrayBuffer(file);
    return new Promise((resolve, reject) => {
      reader.onload = (evt: any) => {
        const result: ArrayBuffer = evt.target.result;
        resolve(result);
      };
      reader.onerror = err => reject(err);
      reader.onabort = err => reject(err);
    });
  }

  /**
   * Renames name property of the passed in file object
   * @param {File} file File object whose name property is to be changed
   * @param {String} newFileName A new File Name
   * @returns {File} A new File Object with new name
   * ```ts
   * // returns a new File Object which has a new name
   * renameFile(fileObject, 'newName');
   * ```
   */
  renameFile(file: File, newFileName: string): File {
    return new File([file], newFileName, { type: file.type });
  }

  /**
   * Returns timestamp
   * @private
   * @returns {String|Number} Timestamp
   * ```ts
   * // returns 17561680000001797
   * getTimeStamp()
   * ```
   */
  private gateTimeStamp(): String | Number {
    if (window.performance) {
      return performance
        .now()
        .toString()
        .replace(/\./, '');
    } else {
      return Date.now();
    }
  }

  /**
   * @private
   * Generates a random string
   * @returns {String} a random string
   * ```ts
   * // returns a random string like: 598hqcy9x3k
   * getRandomString()
   * ```
   */
  private getRandomString(): string {
    return Math.random()
      .toString(36)
      .substr(2);
  }

  toTitleCase(str?: any) {
    str = str || '';
    return str.replace(/\w\S*/g, function(txt) {
      return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
    });
  }

  getCurrentOrgName(user: UserSession) {
    let orgName = '';
    if (user.orgList) {
      const currentOrg = user.orgList.find(org => org.orgId === user.orgId);
      orgName = currentOrg && currentOrg.orgName;
    }
    return orgName;
  }

  convertToDataURLviaCanvas(url) {
    return new Promise<string>((resolve, reject) => {
      const img = new Image();
      img.crossOrigin = 'Anonymous';
      img.onload = function() {
        let canvas: any = <HTMLCanvasElement>document.createElement('CANVAS');
        const ctx = canvas.getContext('2d');
        let dataURL;
        canvas.height = img.naturalHeight;
        canvas.width = img.naturalWidth;
        ctx.drawImage(this, 0, 0);
        dataURL = canvas.toDataURL();
        canvas = null;
        resolve(dataURL);
      };
      img.src = url;
    });
  }

  /**
   * Returns a function that runs only once
   * @param fn
   * @param context
   */
  once(fn, context) {
    let result;
    return function() {
      if (fn) {
        result = fn.apply(context || this, arguments);
        fn = null;
      }
      return result;
    };
  }

  numberToWords(data) {
    return this.toWords(data);
  }

  toWords(inputNumber): string {
    const str = `${inputNumber}`;
    const splt = str.split('').map(val => +val);
    const rev = splt.reverse();
    const once = ['Zero', ' One', ' Two', ' Three', ' Four', ' Five', ' Six', ' Seven', ' Eight', ' Nine'];
    const twos = ['Ten', ' Eleven', ' Twelve', ' Thirteen', ' Fourteen', ' Fifteen', ' Sixteen', ' Seventeen', ' Eighteen', ' Nineteen'];
    const tens = ['', 'Ten', ' Twenty', ' Thirty', ' Forty', ' Fifty', ' Sixty', ' Seventy', ' Eighty', ' Ninety'];

    const numLength = rev.length;
    const word = [];
    let j = 0, i = 0;

    for (i = 0; i < numLength; i++) {
      switch (i) {
        case 0:
          if ((rev[i] === 0) || (rev[i + 1] === 1)) {
            word[j] = '';
          } else {
            word[j] = '' + once[rev[i]];
          }
          word[j] = word[j];
          break;
        case 1:
          aboveTens();
          break;
        case 2:
          if (rev[i] === 0) {
            word[j] = '';
          } else if ((rev[i - 1] === 0) || (rev[i - 2] === 0)) {
            word[j] = once[rev[i]] + ' Hundred ';
          } else {
            word[j] = once[rev[i]] + ' Hundred and';
          }
          break;
        case 3:
          if (rev[i] === 0 || rev[i + 1] === 1) {
            word[j] = '';
          } else {
            word[j] = once[rev[i]];
          } if ((rev[i + 1] !== 0) || (rev[i] > 0)) {
            word[j] = word[j] + ' Thousand';
          }
          break;
        case 4:
          aboveTens();
          break;
        case 5:
          if ((rev[i] === 0) || (rev[i + 1] === 1)) {
            word[j] = '';
          } else {
            word[j] = once[rev[i]];
          }
          if (rev[i + 1] !== 0 || rev[i] > 0) {
            word[j] = word[j] + ' Lakh';
          }
          break;
        case 6:
          aboveTens();
          break;
        case 7:
          if ((rev[i] === 0) || (rev[i + 1] === 1)) {
            word[j] = '';
          } else {
            word[j] = once[rev[i]];
          }
          if (rev[i + 1] !== 0 || rev[i] > 0) {
            word[j] = word[j] + ' Crore';
          }
          break;
        case 8:
          aboveTens();
          break;
        default: break;
      }
      j++;
    }

    function aboveTens() {
      if (rev[i] === 0) {
        word[j] = '';
      } else if (rev[i] === 1) {
        word[j] = twos[rev[i - 1]];
      } else {
        word[j] = tens[rev[i]];
      }
    }

    word.reverse();
    let finalOutput = '';
    for (i = 0; i < numLength; i++) {
      finalOutput = finalOutput + word[i];
    }
    return finalOutput.trim();
  }

  groupBy(array: Array<any>, prop: string): Object {
    return array.reduce((groups, item) => {
      const val = item[prop];
      groups[val] = groups[val] || [];
      groups[val].push(item);
      return groups;
    }, {});
  }

  typeOf(obj): string {
    return Object.prototype.toString
      .apply(obj)
      .slice(8, -1)
      .toLowerCase();
  }

  dataURItoBlob(dataURI) {
    // convert base64 to raw binary data held in a string
    const byteString = atob(dataURI.split(',')[1]);

    // separate out the mime component
    const mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];

    // write the bytes of the string to an ArrayBuffer
    const arrayBuffer = new ArrayBuffer(byteString.length);
    const _ia = new Uint8Array(arrayBuffer);
    for (let i = 0; i < byteString.length; i++) {
        _ia[i] = byteString.charCodeAt(i);
    }

    const dataView = new DataView(arrayBuffer);
    const blob = new Blob([dataView], { type: mimeString });
    return blob;
  }

}
