import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { Idle } from '@ng-idle/core';
import {
  BLURSCREEN_PASSWORD_CONSTANT_TOKEN,
  IBlurScreenPasswordPromptConstants
} from './blur-screen-password-prompt.constant';
import { blurScreenPasswordPromptConstantTokenProvider } from './blur-screen-password-prompt.constant';
import { FormBuilder, AbstractControl, Validators } from '@angular/forms';
import { UserService, LoginApiService } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { finalize } from 'rxjs/operators';
import { UserSession } from '@core/models/user-session';
import { BlurScreenPasswordPromptService } from './blur-screen-password-prompt.service';

@Component({
  selector: 'app-blur-screen-password-prompt',
  templateUrl: './blur-screen-password-prompt.component.html',
  styleUrls: ['./blur-screen-password-prompt.component.scss'],
  providers: [
    blurScreenPasswordPromptConstantTokenProvider,
    BlurScreenPasswordPromptService
  ]
})
export class BlurScreenPasswordPromptComponent implements OnInit {
  enabled = false;
  inputType;
  inputPlaceholder;
  passwordInputFieldValue;
  inputMaxLength;
  otpEnabled = false;
  showResendOtpText = false;
  passwordForm: AbstractControl;
  isRegenerate = false;
  currentUser: UserSession;
  otpTextTimoutId;

  @Output()
  onSuccess: EventEmitter<boolean> = new EventEmitter<boolean>();

  @Output()
  onPrestateAvail: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor(
    private readonly idle: Idle,
    private blurScreenPasswordPromptService: BlurScreenPasswordPromptService,
    @Inject(BLURSCREEN_PASSWORD_CONSTANT_TOKEN)
    private constants: IBlurScreenPasswordPromptConstants,
    private fb: FormBuilder,
    private loginService: LoginApiService,
    private userService: UserService,
    private toastr: ToastrService
  ) { }

  ngOnInit() {
    this.initPasswordForm();
    this.toggleInputType();
    this.userService.userSource$.subscribe(user => {
      const promptState = this.blurScreenPasswordPromptService.getPromptState();
      this.currentUser = user;
      if (!user) {
        if (this.idle.isRunning) {
          this.idle.stop();
        }
        this.enabled = false;
        this.blurScreenPasswordPromptService.savePromptState(false);
      } else {
        // if prompt state is present and is enable don't start the idle
        if (promptState && promptState.enabled) {
          this.enabled = true;
          this.onPrestateAvail.emit(true);
        } else {
          this.idle.stop();
          this.idle.watch();
        }
      }
    });

    // to handle the log-out condition at that time user data will become null
    this.idle.onTimeout.subscribe(() => {
      this.otpEnabled = false;
      this.toggleInputType();
      this.enabled = true;
      this.blurScreenPasswordPromptService.savePromptState(true);
    });

  }

  toggleInputType() {
    const metaData = this.constants.INPUT_METADATA[+this.otpEnabled];
    this.inputPlaceholder = metaData.placeholder;
    this.inputMaxLength = metaData.maxLength;
    this.inputType = metaData.type;
    this.passwordInputFieldValue = null;
  }

  getProfileImage() {
    if (this.currentUser && this.currentUser.imagePath) {
      return { 'background-image': `url('${this.currentUser.imagePath}')` };
    }
  }

  toggleMethod() {
    const otpRetryTimeout = this.constants.OTP_RETRY_TIMEOUT;
    this.showResendOtpText = false;
    this.passwordForm.get('passwordInput').setValue('');
    if (this.otpTextTimoutId) {
      window.clearTimeout(this.otpTextTimoutId);
    }
    this.otpEnabled = !this.otpEnabled;
    this.toggleInputType();
    if (this.otpEnabled) {
      this.sendOtp();
    }
    this.otpTextTimoutId = setTimeout(() => {
      this.showResendOtpText = true;
    }, otpRetryTimeout);
  }

  // get the otp (API integration)
  sendOtp() {
    const payload = {
      userName: this.currentUser.userName,
      isRegenerate: this.isRegenerate,
      empPatientInd: 'u'
    };
    this.loginService.getOtp(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.isRegenerate = true;
          this.toastr.success('success', response.responseMessage);
        } else if (response.responseCode === 'E400') {
          this.toastr.error('error', response.responseMessage);
        }
      },
      err => {
        const { error: { responseMessage } } = err;
        this.toastr.error(responseMessage, 'Error');
      }
    );
  }

  loginPasswordVerification() {
    const payload = {
      pasword: this.passwordForm.get('passwordInput').value,
      authenticatedUserId: this.currentUser.regId,
      orgId: this.currentUser.orgId
    };
    this.loginService
      .checkBlurScreenPswAuthentication(payload)
      .pipe(
        finalize(() => {
          this.passwordForm.patchValue({
            passwordInput: ''
          });
        })
      )
      .subscribe(
        response => {
          if (response.responseCode === 'E200') {
            if (response.data) {
              // this.toastr.success('Success', response.responseMessage);
              this.enabled = false;
              this.idle.watch();
              this.onSuccess.emit(true);
              this.blurScreenPasswordPromptService.savePromptState(false);
              this.deleteFailureLoginAttemptInBlureScrean();
            }
          } else if (response.responseCode === 'E400') {
            this.saveFailureLoginAttemptInBlureScrean();
            this.toastr.error('error', response.responseMessage);
          }
        },
        (err) => {
          const { error: { responseMessage } } = err;
          this.saveFailureLoginAttemptInBlureScrean();
          this.toastr.error('error', responseMessage);
        }
      );
  }

  loginOtpVerification() {
    const payload = {
      otp: this.passwordForm.value.passwordInput,
      authenticatedUserId: this.currentUser.regId,
      orgId: this.currentUser.orgId
    };
    this.loginService
      .checkBlurScreenOtpAuthentication(payload)
      .pipe(
        finalize(() => {
          this.passwordForm.patchValue({
            passwordInput: ''
          });
        })
      )
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          if (response.data) {
            this.enabled = false;
            this.otpEnabled = false;
            this.idle.watch();
            this.onSuccess.emit(true);
            this.blurScreenPasswordPromptService.savePromptState(false);
            this.deleteFailureLoginAttemptInBlureScrean();
          }
        } else if (response.responseCode === 'E400') {
          this.saveFailureLoginAttemptInBlureScrean();
          this.toastr.error('error', response.responseMessage);
        }
      }, (err) => {
        this.toastr.error('error', err.responseMessage);
        this.saveFailureLoginAttemptInBlureScrean();
      });
  }

  saveFailureLoginAttemptInBlureScrean() {
    const payload = {
      authenticatedUserId: this.currentUser.regId,
      orgId: this.currentUser.orgId
    };
    this.loginService
      .saveFailureLoginAttemptInBlureScrean(payload)
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
        } else if (response.responseCode === 'E400') {
          this.logout();
        }
      }, this.logout.bind(this));
  }

  //  enabling screen when user enter correct password
  deleteFailureLoginAttemptInBlureScrean() {
    const payload = {
      authenticatedUserId: this.currentUser.regId,
      orgId: this.currentUser.orgId
    };
    this.loginService
      .deleteFailureLoginAttemptInBlurScreen(payload)
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
        } else if (response.responseCode === 'E400') {
          this.logout();
        }
      }, this.logout.bind(this));
  }

  logout() {
    const payload = {
      regId: this.currentUser.regId,
      orgId: this.currentUser.orgId,
      acctType: this.currentUser.accountType
    };
    this.loginService
      .logout(payload)
      .pipe(
        finalize(() => {
          this.userService.removeUser();
        })
      )
      .subscribe();
  }

  reEnableTimer() {
    this.enabled = false;
    this.idle.watch();
    this.onSuccess.emit(true);
  }

  doContinue() {
    if (this.otpEnabled) {
      this.loginOtpVerification();
    } else {
      this.loginPasswordVerification();
    }
  }

  initPasswordForm() {
    this.passwordForm = this.fb.group({
      passwordInput: ['', Validators.required]
    });
  }
}
