import { Injectable, Inject } from '@angular/core';
import { ApiService } from '@core/services/api.service';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';

@Injectable({
  providedIn: 'root'
})
export class ImagingApiService {
  readonly MODULE_NAME = 'IMAGING';

  constructor(
    @Inject(URL_CONSTANTS_TOKEN) private urlConstants: UrlConstants,
    private apiService: ApiService
  ) {}

  fetchDrugRouteTypes<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.FETCH_ROUTE_TYPES,
      payload
    );
  }

  fetchGivenByList<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.FETCH_GIVEN_BY,
      payload
    );
  }

  fetchImagingChartResponse<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.FETCH_IMAGING_CHART_RESPONSE,
      payload
    );
  }

  fetchOrderEntryBasedOnSearchFilter<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.FETCH_ORDER_ENTRY_BASED_ON_SEARCH_FILTERS,
      payload
    );
  }

  fetchRadioLogyTestImagePaths<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.FETCH_RAGIOLOGY_TEST_IMAGE_PATHS,
      payload
    );
  }

  fetchTemplateIdForGivenTest<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.FETCH_TEMPLATE_ID_FOR_GIVEN_TEST_ID,
      payload
    );
  }

  fetchTemplateColumns<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.FETCH_TEMPLATE_COLUMNS,
      payload
    );
  }

  insertImagingContrastData<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.INSERT_CONTRAST_DATA,
      payload
    );
  }
  getEmpListBasedOnDesignation<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.GET_EMP_LIST_BASED_ON_DESIGNATION,
      payload
    );
  }
  saveUploadedImagePaths<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.SAVE_UPLOADED_IMAGE_PATHS,
      payload
    );
  }

  updateOrderTestData<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.IMAGING.UPDATE_ORDER_TEST_DATA,
      payload
    );
  }
}
