import { Injectable, Inject } from '@angular/core';
import { ZuulService } from './zuul.service';
import { UserService } from './user.service';
import { catchError, concatMap, takeUntil } from 'rxjs/operators';
import { of, timer, Subject } from 'rxjs';
import { AppConstant } from '@core/models/app-constant';
import { APP_CONSTANTS } from '@app/app.constant';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private readonly PINGER_INTERVAL;
  private readonly pingerErrorNotifier = new Subject();

  constructor(
    @Inject(APP_CONSTANTS) appConstants: AppConstant,
    private zuul: ZuulService,
    private userService: UserService
  ) {
    this.PINGER_INTERVAL = appConstants.PINGER_INTERVAL;
  }

  startPinger() {
    // tslint:disable-next-line:no-console
    console.info('starting pinger');
    const userId = this.userService.getUserTokenDetails().userId;
    // stoping if an earlier pinger is running
    this.pingerErrorNotifier.next();
    timer(0, this.PINGER_INTERVAL)
      .pipe(
        concatMap(_ => this.zuul.pinger(userId)),
        catchError(err => {
          this.userService.removeUser();
          this.pingerErrorNotifier.next();
          // tslint:disable-next-line:no-console
          console.info('Pinger will be stopped');
          return of(null);
        }),
        takeUntil(this.pingerErrorNotifier)
      )
      .subscribe();
  }

  stopPinger() {
    // tslint:disable-next-line:no-console
    console.info('stopping pinger');
    this.pingerErrorNotifier.next();
  }
}
