import { Action } from '@ngrx/store';

export enum LayoutActionTypes {
  SideNavTransitionEnd = '[layout] sidenav transitionend',
  FetchSideNav = '[sideNav] fetchSideNav',
  FetchSideNavSuccess = '[sideNav] fetchSideNavSuccess',
  SetProfileImage = '[ProfileImage] set profile image',
  ToggleSideNav = '[Layout] toggle side nav',
  PinnedItems = '[Layout] pinned sidenav items',
  CurrentUnPinnedItem = '[Layout] Current Unpinned items'
}

export class SetProfileImage implements Action {
  readonly type = LayoutActionTypes.SetProfileImage;
  constructor(public payload: string) {}
}

export class ToggleSideNav implements Action {
  readonly type = LayoutActionTypes.ToggleSideNav;
  constructor(public payload: boolean) { }
}

export class PinnedScreens implements Action {
  readonly type = LayoutActionTypes.PinnedItems;
  constructor(public payload: any) { }
}

export class CurrentUnPinnedItem implements Action {
  readonly type = LayoutActionTypes.CurrentUnPinnedItem;
  constructor(public payload: any) { }
}

export type LayoutActions =  SetProfileImage | ToggleSideNav | PinnedScreens | CurrentUnPinnedItem;
