import { Injectable, Inject } from '@angular/core';
import { ApiService } from '@core/services/api.service';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';

@Injectable({
  providedIn: 'root'
})
export class BillingApiService {
  readonly MODULE_NAME = 'BILLING';

  constructor(
      @Inject(URL_CONSTANTS_TOKEN) private urlConstants: UrlConstants,
      private apiService: ApiService
  ) { }

  getServiceUnitPrice<T>(payload) {
    return this.apiService.post<T>(
        this.MODULE_NAME,
        this.urlConstants.BILLING.GET_SERVICE_UNIT_PRICE,
        payload
    );
  }

  listOfAmountDefinedServices<T>(payload) {
    return this.apiService.post<T>(
        this.MODULE_NAME,
        this.urlConstants.BILLING.LIST_OF_AMOUNT_DEFINED_SERVICES_PROVIDED,
        payload
    );
  }

}
