import { NgModule, NO_ERRORS_SCHEMA, Optional, SkipSelf } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgProgressModule } from '@ngx-progressbar/core';
import { ModuleWithProviders } from '@angular/compiler/src/core';
import { CommonModule } from '@angular/common';
import { AppComponent } from './components/app.component';
import { LayoutComponent } from './components/layout/layout.component';
import { TopNavComponent } from './components/top-nav/top-nav.component';
import { SideNavBarComponent } from './components/side-nav/side-nav.component';
import { NoAccessComponent } from './components/no-access/no-access.component';
import { ReactiveFormsModule } from '@angular/forms';

import {
  momentProvider,
  TimeStampInterceptor,
  jsZipProvider,
  sparkMd5Provider,
  cornerStoneProvider,
  cornerstoneToolsProvider,
  windowProvider,
  pdfMakeProvider,
  jsBarCodeProvider,
  highchartsProvider,
  cornerstoneMathProvider,
  conerstoneWadoImageLoaderProvider,
  cloneDeepProvider,
  croppieProvider,
  printSettingsDefaultsProvider,
  arrayIntersectorProvider,
  mergeDeepProvider,
  scrollContainerProvider,
  AppointmentApiService,
  RegisterationBillingService
} from '@core/services';
import { URL_CONSTANTS, URL_CONSTANTS_TOKEN } from './url.constant';
import { ScrollToTopComponent } from '@core/components/scroll-to-top/scroll-to-top.component';
import { eyeExamCodeProvider } from '@core/services/eye-exam-fied-codes.service';
import { AuthInterceptorService } from './services/auth-interceptor.service';
import { BlurScreenPasswordPromptComponent } from './components/blur-screen-password-prompt/blur-screen-password-prompt.component';
import { ProfileImageSelectorComponent } from './components/profile-image/profile-image.component';

@NgModule({
  imports: [
    RouterModule,
    HttpClientModule,
    NgProgressModule,
    CommonModule,
    ReactiveFormsModule
  ],
  declarations: [
    AppComponent,
    LayoutComponent,
    ScrollToTopComponent,
    TopNavComponent,
    SideNavBarComponent,
    NoAccessComponent,
    BlurScreenPasswordPromptComponent,
    ProfileImageSelectorComponent,
  ],
  entryComponents: [
    ProfileImageSelectorComponent,
  ],
  exports: [
    ScrollToTopComponent,
    AppComponent,
    NoAccessComponent,
    LayoutComponent,
    TopNavComponent,
    SideNavBarComponent
  ],
  schemas: [NO_ERRORS_SCHEMA]
})
export class CoreModule {
  constructor(
    @Optional()
    @SkipSelf()
    parentModule: CoreModule
  ) {
    if (parentModule) {
      throw new Error(
        `Core Module is already loaded. Please import it only in AppModule`
      );
    }
  }
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: CoreModule,
      providers: [
        momentProvider,
        jsZipProvider,
        pdfMakeProvider,
        sparkMd5Provider,
        cornerstoneToolsProvider,
        cornerstoneMathProvider,
        conerstoneWadoImageLoaderProvider,
        cornerStoneProvider,
        windowProvider,
        highchartsProvider,
        jsBarCodeProvider,
        cloneDeepProvider,
        croppieProvider,
        printSettingsDefaultsProvider,
        scrollContainerProvider,
        eyeExamCodeProvider,
        arrayIntersectorProvider,
        mergeDeepProvider,
        AppointmentApiService,
        RegisterationBillingService,
        {
          provide: URL_CONSTANTS_TOKEN,
          useValue: URL_CONSTANTS
        },
        {
          provide: HTTP_INTERCEPTORS,
          useClass: TimeStampInterceptor,
          multi: true
        },
        {
          provide: HTTP_INTERCEPTORS,
          useClass: AuthInterceptorService,
          multi: true
        }
      ]
    };
  }
}
