import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { APP_CONSTANTS } from '@app/app.constant';
import { AppConstant } from '@core/models/app-constant';
import { ApiResponse } from '../models/api-response';
import { UserService } from './user.service';
import { UserSession } from '@core/models/user-session';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  private baseUrl: string;
  private userSession: UserSession;
  constructor(
    private http: HttpClient,
    @Inject(APP_CONSTANTS) private appConstants: AppConstant,
    private userService: UserService
  ) {
    this.baseUrl = `${appConstants.PROTOCOL}://${appConstants.IP}:`;
    this.userSession = this.userService.getUser();
  }

  get<U>(moduleName: string, action: string, options = {}) {
    const method = 'GET';
    const path = this.resolvePath(moduleName, action);
    const url = `${this.baseUrl}${path}`;
    return this.http.request<ApiResponse<U> | ArrayBuffer>(method, url, options);
  }

  post<U>(moduleName: string, action: string, body: any, options = {}) {
    const method = 'POST';
    const path = this.resolvePath(moduleName, action);
    const url = `${this.baseUrl}${path}`;
    if (moduleName === 'WAREHOUSE') {
      body.orgTimeZone = this.userSession.orgTimeZone;
    }
    return this.http.request<ApiResponse<U>>(method, url, {
      body,
      ...options
    });
  }

  private resolvePath(moduleName: string, action: string) {
    return `${this.appConstants.API_PATHS[moduleName]}/${action}`;
  }
}
