import { Injectable } from '@angular/core';
import { SessionStorageService } from 'ngx-webstorage';
import { UserSession } from '@core/models/user-session';
import { ReplaySubject } from 'rxjs';
import { IUserToken } from '@core/models/user-token';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  readonly USER_SESSION = 'user_session';
  readonly USER_TOKEN_DETAILS = 'user_token_details';

  private userSource = new ReplaySubject<UserSession>();
  private userTokenSource = new ReplaySubject<IUserToken>();

  /**
   * An Observable that emits current user's session
   * ```ts
    // example usage
    userSource$.subscribe(userSession => {
      if (userSession.imagePath) {
        this.profileImageLocation = userSession.imagePath;
      } else if (userSession.doctorObj.doc_img) {
        this.profileImageLocation = userSession.doctorObj.doc_img;
      }
    });
   * ```
   */
  userSource$ = this.userSource.asObservable();
  userTokenSource$ = this.userSource.asObservable();

  constructor(private storage: SessionStorageService) { }

  /**
   * This method returns the current user's session data
   * @returns {UserSession} The current user's session data
   */
  getUser(): UserSession {
    const user = this.storage.retrieve(this.USER_SESSION);
    return user;
  }

  /**
   * This method sets the current user
   * @param {UserSession} user User's session data
   */
  setUser(user: UserSession) {
    this.storage.store(this.USER_SESSION, user);
    this.userSource.next(user);
  }

  /**
   * This method returns the current user's token details
   */
  getUserTokenDetails() {
    return <IUserToken>this.storage.retrieve(this.USER_TOKEN_DETAILS);
  }

  /**
   * This method sets the current user's token details
   */
  setUserTokenDetails(userToken: IUserToken) {
    this.storage.store(this.USER_TOKEN_DETAILS, userToken);
    this.userTokenSource.next(userToken);
  }

  /**
   * this method removes the current user's details
   */
  removeUser() {
    this.storage.clear();
    this.userSource.next(null);
    this.userTokenSource.next(null);
  }

}
