import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpHandler, HttpRequest, HttpErrorResponse } from '@angular/common/http';
import { UserService } from './user.service';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class AuthInterceptorService implements HttpInterceptor {

  constructor(
    private userService: UserService,
    private authService: AuthService
  ) { }

  intercept(req: HttpRequest<any>, next: HttpHandler) {

    let clonedReq = req.clone();
    if (req.url.slice(0, 23) !== 'https://localhost:15005') {
      if (!/(assets)|(\.json)/.test(req.url)) {
        const userSessionDetails = this.userService.getUserTokenDetails();
        clonedReq = req.clone({
          headers: req.headers
            .set('X-Authorization', userSessionDetails.token)
            .set('X-Authorization-user', userSessionDetails.userId)
        });
      }
    }
    return next.handle(clonedReq)
      .pipe(
        catchError((err) => {
          if (err instanceof (HttpErrorResponse)) {
            if (err.error && (err.error.responseCode === 'Z5678' || err.error.responseCode === 'Z5679')) {
              // clears the session storage
              this.userService.removeUser();
            }
          }
          return throwError(err);
        })
      );
  }
}
