import { Injectable, Inject } from '@angular/core';
import { ApiService } from '@core/services/api.service';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';

@Injectable({
  providedIn: 'root'
})
export class ReportsService {

  private readonly MODULE_NAME = 'REPORTS';

  constructor(
    @Inject(URL_CONSTANTS_TOKEN) private urlConstants: UrlConstants,
    private apiService: ApiService
  ) {}

  fetchDoctorWiseAppointsBasedOnDayWeekMonth<T>(params) {
    return this.apiService.get<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_OPTICAL_ORDER_SALE_REPORT,
      { params, responseType: 'arraybuffer' }
    );
  }
  fetchOpticalOrderSaleReport<T>(params) {
    return this.apiService.get<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_OPTICAL_ORDER_SALE_REPORT,
      { params, responseType: 'arraybuffer' }
    );
  }
  fetchOpticalPosReport<T>(params) {
    return this.apiService.get<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_OPTICAL_POS_REPORT,
      { params, responseType: 'arraybuffer' }
    );
  }
  fetchOpticalWorkSopReferenceReport<T>(params) {
    return this.apiService.get<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_OPTICAL_WORK_SOP_REFERENCE_REPORT,
      {params, responseType: 'arraybuffer' }
    );
  }
  fetchOpticalPosReturnsReport<T>(params) {
    return this.apiService.get<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_OPTICAL_POS_RETURNS_REPORT,
      {params, responseType: 'arraybuffer'}
    );
  }
  fetchOpBillPrint<T>(params){
    return this.apiService.get<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_OP_BILL_PRINT,
      {params, responseType: 'arraybuffer'}
    );
  }
  fetchWarehouseSaleOrderPrint<T>(params){
    return this.apiService.get<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_WAREHOUSE_SALE_ORDER_PRINT,
      {params, responseType: 'arraybuffer'}
    );
  }
  fetchWarehouseCreditNoteOrderPrint<T>(params) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_WAREHOUSE_CREDIT_NOTE_ORDER_PRINT,
      { params, responseType: 'arraybuffer' }
    );
  }
  fetchWarehouseOrderPicklistPrint<T>(params) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_WAREHOUSE_SALE_ORDER_PRINT,
      { params, responseType: 'arraybuffer' }
    );
  }
  fetchWarehouseReturnsOrderPrint<T>(params) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_WAREHOUSE_RETURNS_ORDER_PRINT,
      { params, responseType: 'arraybuffer' }
    );
  }
  fetchWarehouseOrderPackedlistPrint<T>(params) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.REPORTS.FETCH_WAREHOUSE_ORDER_PACKEDLIST_PRINT,
      { params, responseType: 'arraybuffer' }
    );
  }
}
