import { Injectable, Inject } from '@angular/core';
import { ApiService } from '@core/services/api.service';
import { Observable } from 'rxjs';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';

@Injectable({
  providedIn: 'root'
})
export class InventoryApiService {
  private readonly MODULE_NAME = 'INVENTORY';
  constructor(
    @Inject(URL_CONSTANTS_TOKEN) private urlConstants: UrlConstants,
    private apiService: ApiService
  ) {}

  insertAssest<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.INSERT_ASSET,
      payload
    );
  }

  fetchAsset<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.FETCH_ASSET,
      payload
    );
  }

  fetchEmployeeList<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.FETCH_EMPLOYEE_LIST,
      payload
    );
  }
  fetchAssetTypes<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.FETCH_ASSET_TYPES,
      payload
    );
  }
  InsertAssetTypes<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.INSERT_ASSET_TYPES,
      payload
    );
  }
  InsertAssetLocation<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.SAVE_ASSET_LOCATION,
      payload
    );
  }
  fetchSubBranchOrgNames<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.FETCH_PLANNED_EMPLOYEE,
      payload
    );
  }

  fetchShiftLocationData<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.FETCH_ASSET_LOCATION,
      payload
    );
  }

  insertOrUpdateAssetsWorkOrderInfo<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.INSERT_WORK_ORDER,
      payload
    );
  }

  fetchAssetsWorkOrderInfo<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.FETCH_WORK_ORDER,
      payload
    );
  }

  fetchPlannedMaintenanceAssetNames<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.FETCH_PLANNED_MAINTENANCE,
      payload
    );
  }

  deleteAssetInformation<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.INVENTORY.DELETE_ASSET_INFORMATION,
      payload
    );
  }
}
