import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { UserService } from './user.service';
import { tap, catchError, map } from 'rxjs/operators';
import { of } from 'rxjs';
import { LoginApiService } from './login-api.service';
import { ApiResponse } from '@core/models/api-response';

@Injectable({
  providedIn: 'root'
})
export class UserResolverService implements Resolve<ApiResponse<string>> {
  constructor(
    private loginApiService: LoginApiService,
    private userService: UserService
  ) {}

  resolve() {
    const tokenDetails = this.userService.getUserTokenDetails();
    const payload = {
      authenticatedUserId: parseInt(tokenDetails.userId, 10),
      orgId: tokenDetails.orgId,
      payLoad: ''
    };
    return this.loginApiService.fetchUserDetails<string>(payload).pipe(
      tap((userDetails) => {
        if (userDetails) {
          const user = JSON.parse(userDetails.data);
          this.userService.setUser(user);
        }
      }),
      map((repsonse) => {
        if (repsonse.responseCode === 'E200') {
          return JSON.parse(repsonse.data);
        } else if (repsonse.responseCode === 'E400') {
          throw Error(repsonse.responseMessage);
        }
      }),
      catchError(() => {
        return of(null);
      })
    );
  }
}
