import { InjectionToken, FactoryProvider } from '@angular/core';

export interface ISidenavConfigs {
  TOGGLER_BTN_ID: string;
  MAX_DEPTH: number;
  MOD_VERSION: number;
}

export const SIDENAV_CONFIG_TOKEN: InjectionToken<
  ISidenavConfigs
> = new InjectionToken<ISidenavConfigs>('sidenav configurations');

const configFactory = () => ({
  TOGGLER_BTN_ID: 'sidenav-toggler',
  MAX_DEPTH: 2,
  MOD_VERSION: 6
});
export const sidenavConfigProvider: FactoryProvider = {
  provide: SIDENAV_CONFIG_TOKEN,
  useFactory: configFactory
};

export default { sidenavConfigProvider, SIDENAV_CONFIG_TOKEN };
