import { InjectionToken, FactoryProvider } from '@angular/core';

export interface IBlurScreenPasswordPromptConstants {
  INPUT_METADATA: Array<IBlurScreenPasswordPromptConstantsInputMetaData>;
  OTP_RETRY_TIMEOUT: number;
}

export interface IBlurScreenPasswordPromptConstantsInputMetaData {
  placeholder: string;
  maxLength: number;
  type: string;
}

const constants = {
  INPUT_METADATA: [
      { placeholder: 'Enter Password', maxLength: 20, type: 'password' },
      { placeholder: 'Enter OTP', maxLength: 6, type: 'text' }
  ],
  OTP_RETRY_TIMEOUT: 5000,
};

export const BLURSCREEN_PASSWORD_CONSTANT_TOKEN = new InjectionToken<IBlurScreenPasswordPromptConstants>('blurScreenConstants');

export const blurscreenPasswordConstantsFactory = () => constants;

export const blurScreenPasswordPromptConstantTokenProvider: FactoryProvider = {
  provide: BLURSCREEN_PASSWORD_CONSTANT_TOKEN,
  useFactory: blurscreenPasswordConstantsFactory
};
