import {
  Component,
  OnDestroy,
  OnInit,
  ViewChild,
  Inject,
  PLATFORM_ID,
  Renderer2,
  ElementRef
} from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { filter, map, mergeMap } from 'rxjs/operators';
import { Title } from '@angular/platform-browser';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { UserService } from '@core/services/user.service';
import { Location, isPlatformBrowser, DOCUMENT } from '@angular/common';
import {
  UtilService,
  CORNERSTONE_WADO_IMAGE_LOADER,
  CORNERSTONE_MATH,
  CORNERSTONE, CORNERSTONETOOLS
} from '@core/services';

import { APP_CONSTANTS } from '@app/app.constant';
import { AppConstant } from '@core/models/app-constant';
import { TranslateService } from '@ngx-translate/core';
import {
  IInitialEncodedParams,
} from '@core/models/initialParams';
import { IUserToken } from '@core/models/user-token';
import { Idle, DEFAULT_INTERRUPTSOURCES } from '@ng-idle/core';
import { AuthService } from '@core/services/auth.service';

@AutoUnsubscribe()
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnDestroy, OnInit {

  readonly IDLE_TIME;
  readonly TIME_OUT_TIME;

  @ViewChild('blurWrapper') blurWrapper: ElementRef;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    @Inject(PLATFORM_ID) private platformId,
    @Inject(APP_CONSTANTS) private appConstants: AppConstant,
    @Inject(CORNERSTONE_WADO_IMAGE_LOADER) private wadoImageLoader,
    @Inject(CORNERSTONE_MATH) private cornerstoneMath,
    @Inject(CORNERSTONE) private cornerstone,
    @Inject(CORNERSTONETOOLS) private cornerstoneTools,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private title: Title,
    translate: TranslateService,
    private authService: AuthService,
    private location: Location,
    private utilService: UtilService,
    private userService: UserService,
    private renderer: Renderer2,
    private idle: Idle
  ) {
    this.IDLE_TIME = this.appConstants.IDLE_TIME;
    this.TIME_OUT_TIME = this.appConstants.TIME_OUT_TIME;
    translate.use(translate.getBrowserLang() || 'en');
    translate.setDefaultLang('en');
  }

  /**
   * Sets the current user using [setUser]{@link AppComponent#setUser}.
   * Subscribes to the [NavigationEnd]{@link https://angular.io/api/router/NavigationEnd} event to change
   * the document title
   */
  ngOnInit() {
    this.setUserTokenDetails();
    this.initCornerstoneLibs();
    this.setUpIdle();
    this.router.events
      .pipe(
        filter(event => event instanceof NavigationEnd),
        map(() => this.activatedRoute),
        map(route => {
          while (route.firstChild) {
            route = route.firstChild;
          }
          return route;
        }),
        mergeMap(route => route.data)
      )
      .subscribe((routeData) => {
        if (isPlatformBrowser(this.platformId)) {
          window['gtag']('config', this.appConstants.GTAGID, {
            page_path: this.location.path()
          });
        }
        if (!Object.is(routeData, null) || !Object.is(routeData, undefined)) {
          if (routeData.title) {
            this.title.setTitle(`${routeData.title} | Drucare`);
          }
        }
      });
  }

  decodeParseParams(encodedString) {
    return JSON.parse(atob(decodeURIComponent(encodedString)));
  }

  setUserTokenDetails() {
    if (!this.userService.getUserTokenDetails()) {
      const path = this.location.path(true);
      const encodedParams = <IInitialEncodedParams>(
        this.utilService.parseQueryString(path.substring(path.indexOf('?')))
      );
      try {
        const decodedParams = <IUserToken>(
          this.decodeParseParams(encodedParams.data)
        );
        if (!decodedParams.token || !decodedParams.userId) {
          throw new Error('Invalid params data');
        }
        this.userService.setUserTokenDetails(decodedParams);
      } catch (e) {
        this.router.navigate(['no-access']);
      }
    }
  }

  initCornerstoneLibs() {
    this.wadoImageLoader.external.cornerstone = this.cornerstone;
    this.cornerstoneTools.external.cornerstone = this.cornerstone;
    this.cornerstoneTools.external.cornerstoneMath = this.cornerstoneMath;
    const config = {
      webWorkerPath: `${
        this.appConstants.BASE_HREF
        }assets/js/cornerstoneWADOImageLoaderWebWorker.min.js`,
      taskConfiguration: {
        decodeTask: {
          strict: false,
          codecsPath: `${
            this.appConstants.BASE_HREF
            }assets/js/cornerstoneWADOImageLoaderCodecs.min.js`
        }
      }
    };
    this.wadoImageLoader.webWorkerManager.initialize(config);
  }

  setUpIdle() {
    this.idle.setIdle(this.IDLE_TIME);
    this.idle.setTimeout(this.TIME_OUT_TIME);
    this.idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);

    this.userService.userSource$.subscribe((userData) => {
      if (userData) {
        this.authService.startPinger();
      } else {
        this._blurPreProcess('remove');
        this.authService.stopPinger();
        this.router.navigate(['no-access']);
      }
    });

    this.idle.onTimeout.subscribe(() => {
      this._blurPreProcess('add');
    });

  }

  onBlurredPasswordPromptSuccess() {
    this._blurPreProcess('remove');
  }

  onBlurredPasswordPromptPreStateAvail() {
    this._blurPreProcess('add');
  }

  private _blurPreProcess(type: 'add' | 'remove') {
    this.renderer[`${type}Class`](this.blurWrapper.nativeElement, 'blur-wrapper--blurred');
    this.renderer[`${type}Class`](this.document.body, 'overflow-hidden');
  }

  ngOnDestroy() { }

}
