import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './core/components/app.component';
import { CoreModule } from './core/core.module';
import { RouterModule, PreloadAllModules } from '@angular/router';
import { NgProgressModule } from '@ngx-progressbar/core';
import { NgProgressHttpModule } from '@ngx-progressbar/http';
import { NgProgressRouterModule } from '@ngx-progressbar/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ToastrModule } from 'ngx-toastr';
import { DatePipe } from '@angular/common';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { HttpClient } from '@angular/common/http';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { StoreModule } from '@ngrx/store';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { NgxWebstorageModule  } from 'ngx-webstorage';
import { HighchartsChartModule } from 'highcharts-angular';
import { routes } from './routes';
import { CONSTANTS, APP_CONSTANTS } from './app.constant';
import { reducers } from './reducers';

import { environment } from '@environments/environment';
import { MAT_DATE_LOCALE, MatNativeDateModule } from '@angular/material';
import { EffectsModule } from '@ngrx/effects';

import { NgIdleModule } from '@ng-idle/core';

/**
 * Returns a [TranslateHttpLoader]{@link https://github.com/ngx-translate/http-loader}
 * instance which uses http client to load resources
 * @param http
 * @returns {TranslateHttpLoader} A loader for ngx-translate that uses http calls to load language files
 */
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(
    http,
    `${environment.BASE_HREF}${environment.ILN8PATH}`
  );
}


@NgModule({
  imports: [
    MatNativeDateModule,
    BrowserAnimationsModule,
    MatNativeDateModule,
    NgProgressModule,
    BrowserModule,
    NgxWebstorageModule.forRoot({
      prefix: 'dru'
    }),
    CoreModule.forRoot(),
    NgIdleModule.forRoot(),
    RouterModule.forRoot(routes, {
      useHash: true,
      enableTracing: false,
      paramsInheritanceStrategy: 'always'
    }),
    NgProgressRouterModule,
    NgProgressHttpModule,
    ToastrModule.forRoot(),
    EffectsModule.forRoot([]),
    StoreModule.forRoot(reducers),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    StoreDevtoolsModule.instrument({
      maxAge: 25,
      logOnly: !environment.production
    }),
    HighchartsChartModule,
  ],
  providers: [
    DatePipe,
    { provide: APP_CONSTANTS, useValue: CONSTANTS },
    { provide: MAT_DATE_LOCALE, useValue: 'en-GB' }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
