import {
  Component,
  ViewChild,
  ElementRef,
  AfterViewInit,
  OnDestroy,
  Inject
} from '@angular/core';

import { DOCUMENT } from '@angular/common';
import { MatDialogRef } from '@angular/material/dialog';
import { CROPPIE } from '@core/services';
import { ProfileImageSelectorResult } from '@core/models/profile-image-selector';

@Component({
  templateUrl: './profile-image.component.html',
  styleUrls: ['./profile-image.component.scss']
})
export class ProfileImageSelectorComponent implements OnDestroy, AfterViewInit {
  cameraOutputElement: ElementRef<HTMLVideoElement>;
  cameraStream: MediaStream;
  cropperElement: ElementRef<HTMLImageElement>;
  cropperInstance: any;
  isCameraRequired = true;

  /**
   * @ignore
   */
  @ViewChild('cameraOutput')
  private set _cameraOutputElement(element: ElementRef<HTMLVideoElement>) {
    if (this.cameraOutputElement) {
      this.cameraOutputElement.nativeElement.srcObject = null;
    }
    this.cameraOutputElement = element;
    if (!this.isCameraRequired) {
      this.killCamera();
    } else {
      this.initCamera();
    }
  }

  /**
   * @ignore
   */
  @ViewChild('cropperElement')
  private set _cropperElement(element: ElementRef<HTMLImageElement>) {
    this.cropperElement = element;
    if (element) {
      this.initCropper();
    } else {
      this.killCropper();
      this.cropperInstance = null;
    }
  }

  constructor(
    @Inject(CROPPIE) private croppie,
    @Inject(DOCUMENT) private document: Document,
    private dialogRef: MatDialogRef<
      ProfileImageSelectorComponent,
      ProfileImageSelectorResult
    >
  ) {}

  /**
   * It paints the video element on the canvas and exports the image as a blob
   */
  cameraSnap() {
    this.isCameraRequired = true;
    const cameraOutputNative = this.cameraOutputElement.nativeElement;
    const canvasElement = this.document.createElement('canvas');
    canvasElement.width = cameraOutputNative.videoWidth;
    canvasElement.height = cameraOutputNative.videoHeight;
    const context = canvasElement.getContext('2d');
    context.translate(canvasElement.width, 0);
    context.scale(-1, 1); // required to flip the image
    context.drawImage(cameraOutputNative, 0, 0);
    canvasElement.toBlob(
      blob => {
        this.dismissModal({ profileImage: blob });
      },
      'image/png',
      1
    );
  }

  /**
   * Checks if the browser supports camera
   * @returns {boolean}
   */
  doesItSupportsCamera() {
    return navigator.mediaDevices && navigator.mediaDevices.getUserMedia;
  }

  dismissModal(data?) {
    this.dialogRef.close(data);
  }

  /**
   * Asks user for camera access and starts video streaming from the camera
   * @todo  display the error message on the UI
   */
  getCameraAccess() {
    const cameraOutputNative = this.cameraOutputElement.nativeElement;
    navigator.mediaDevices
      .getUserMedia({ video: { frameRate: { ideal: 30 } } })
      .then(stream => {
        this.cameraStream = stream;
        cameraOutputNative.srcObject = stream;
        cameraOutputNative.onloadedmetadata = () => {
          cameraOutputNative.play();
        };
      })
      .catch(error => {
        console.log(error);
      });
  }

  /**
   * Initializes the camera capture
   * @todo display error messages on UI
   */
  initCamera() {
    if (this.doesItSupportsCamera()) {
      this.getCameraAccess();
    } else {
      console.error('camera is not supported');
    }
  }

  /**
   * initializes the image cropper
   */
  initCropper() {
    const nativeCropper = this.cropperElement.nativeElement;
    this.cropperInstance = new this.croppie(nativeCropper, {
      url: 'assets/images/placeholder-img.png', // default image shown on the image cropper
      viewport: { type: 'circle', width: 250, height: 250 }
    });
  }

  /**
   * closes the streaming tracks
   */
  killCamera() {
    if (this.cameraOutputElement && this.cameraOutputElement.nativeElement) {
      this.cameraOutputElement.nativeElement.srcObject = null;
    }
    if (this.cameraStream) {
      this.cameraStream.getTracks().forEach(track => track.stop());
      this.cameraStream = null;
    }
  }

  /**
   * destroyes the image cropper instance
   */
  killCropper() {
    if (this.cropperInstance) {
      this.cropperInstance.destroy();
    }
  }

  ngAfterViewInit() {
    if (this.isCameraRequired) {
      this.initCamera();
    } else {
      this.initCropper();
    }
  }

  ngOnDestroy() {
    this.killCamera();
    this.killCropper();
  }

  snapCropper() {
    if (this.cropperInstance) {
      this.cropperInstance
        .result({ type: 'blob', size: 'original' })
        .then(croppedBlob => {
          this.dismissModal({ profileImage: croppedBlob });
        });
    }
  }

  updateImageOnCropper($event) {
    const file = $event.target.files[0];
    if (file && this.cropperInstance) {
      const url = URL.createObjectURL(file);
      this.cropperInstance.bind({ url });
    }
  }

  setCameraRequired(required: boolean) {
    this.isCameraRequired = required;
  }
}
