import { Injectable } from '@angular/core';
import { SessionStorageService } from 'ngx-webstorage';

@Injectable()
export class BlurScreenPasswordPromptService {

  private readonly BLUR_STATE_KEY = 'blur-prompt-state';

  constructor(private sessionStorage: SessionStorageService) { }

  savePromptState(enabled: boolean) {
    this.sessionStorage.store(this.BLUR_STATE_KEY, { enabled });
  }

  getPromptState() {
    return this.sessionStorage.retrieve(this.BLUR_STATE_KEY);
  }

}
