import { Injectable, Inject } from '@angular/core';
import { ApiService } from '@core/services/api.service';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';
import { AppConstant } from '@core/models/app-constant';
import { APP_CONSTANTS } from '@app/app.constant';

@Injectable({
  providedIn: 'root'
})
export class EmrApiService {
  readonly MODULE_NAME = 'EMR';

  constructor(
    @Inject(URL_CONSTANTS_TOKEN) private urlConstants: UrlConstants,
    @Inject(APP_CONSTANTS) private appConstants: AppConstant,
    private apiService: ApiService
  ) {}

  fetchDoctorsBasedOnSpecialityIdEmr<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.EMR.FETCH_DOCTORS_BASED_ON_SPECIALITYID_EMR,
      payload
    );
  }
  fetchOrgCustomTabsBasedOnSpeciality<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.EMR.FETCH_ORG_CUSTOM_TABS_BASEDON_SPECIALITY,
      payload
    );
  }

  fetchLatestVisitDetails<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.EMR.FETCH_LATEST_VISIT_DETAILS,
      payload
    );
  }

  fetchCounsellingDetails<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.EMR.FETCH_COUNSELLING_DETAILS,
      payload
    );
  }
  updateCounsellingDetails<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.EMR.UPDATE_COUNSELLING_DETAILS,
      payload
    );
  }

  fetchDepartments<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.EMR.FETCH_DEPARMENTS,
      payload
    );
  }

  fetchDeptDoctorList<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.EMR.GET_EMP_LIST_BASED_ON_DEPT_DESIGNATION,
      payload
    );
  }
  getBucketsConfig() {
    return {
      accessKeyId: this.appConstants.buckets.emrAttachments.accessKeyId,
      secretAccessKey: this.appConstants.buckets.emrAttachments.secretAccessKey,
      region: this.appConstants.buckets.emrAttachments.region
    };
  }

  getBucketName() {
    return {
      bucketURL: this.appConstants.buckets.emrAttachments.BucketURL,
      url: this.appConstants.buckets.emrAttachments.url
    };
  }
}
