import { Injectable, Inject } from '@angular/core';
import { ApiService } from '@core/services/api.service';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';

@Injectable()
export class AppointmentApiService {
  readonly MODULE_NAME = 'APPOINTMENT';

  constructor(
    @Inject(URL_CONSTANTS_TOKEN) private urlConstants: UrlConstants,
    private apiService: ApiService
  ) {}

  fetchDoctorsInOrg<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.FETCHDOCTORSINORG,
      payload
    );
  }
  fetchListOfSpecialities<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.FETCHLISTOFSPECIALITIES,
      payload
    );
  }
  searchPatient<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.FETCH_SEARCH_PATIENT,
      payload
    );
  }
  fetchDoctorSettingsInfo<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.FETCH_DOCTOR_SETTING_INFO,
      payload
    );
  }
  saveDoctorSchedule<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.GET_DOCOTOR_SCHEDULE,
      payload
    );
  }
  listOfAmountDefinedServicesProvided<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.BILLING.LIST_OF_AMOUNT_DEFINED_SERVICES_PROVIDED,
      payload
    );
  }
  bookAppointment<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.SAVE_PATIENT_DATA,
      payload
    );
  }
  cancelAppointment<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.CANCEL_APPOINTMENT,
      payload
    );
  }
  searchCancelPatient<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.SEARCH_CANCEL_PATIENT,
      payload
    );
  }
  fetchDoctorAppointments<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.FETCH_DOCTOR_APPOINTMENTS,
      payload
    );
  }
  fetchMonthWiseAppointments<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.FETCH_MONTH_WISE_DOCTOR_APPOINTMENTS,
      payload
    );
  }
  fetchAppointmentHistory<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.APPOINTMENT_HISTORY,
      payload
    );
  }
  deleteDoctorSchedule<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.DELETE_DOCTOR_SCHEDULE,
      payload
    );
  }
  deleteDoctorAppointments<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.DELETE_DOCTOR_APPOINTMENTS,
      payload
    );
  }
  fetchUnavailableSlots<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.APPOINTMENT.FETCH_UNAVAILABLE_SLOTS,
      payload
    );
  }

}
