// highcharts provider
export const HIGHCHARTS = new InjectionToken('highcharts');
const highchartsFactory = () => {
  const highcharts = require('highcharts');
  const noDataModule = require('highcharts/modules/no-data-to-display.js');
  const drillDown = require('highcharts/modules/drilldown.js');
  const boostModule = require('highcharts/modules/boost.js');
  const roundedCornersPlugin = require('assets/libraries/rounded-corners.js');

  roundedCornersPlugin(highcharts);
  boostModule(highcharts);
  noDataModule(highcharts);
  drillDown(highcharts);

  highcharts.setOptions({
    colors: [
      '#33d9b3',
      '#40417a',
      '#716fd4',
      '#34ace0',
      '#2d2c54',
      '#ff5253',
      '#dcdde1',
      '#ff793f',
      '#feda79',
      '#00a8ff'
    ],
    drilldown: {
      activeAxisLabelStyle: {
        color: '#2d2c54',
        cursor: 'pointer',
        fontWeight: 'bold',
        textDecoration: 'none'
      },
      activeDataLabelStyle: {
        color: '#2d2c54',
        cursor: 'pointer',
        fontWeight: 'bold',
        textDecoration: 'none'
      }
    },
    plotOptions: {
      series: {
        borderRadiusTopLeft: 3,
        borderRadiusTopRight: 3
      }
    },
    xAxis: {
      labels: {
        style: {
          fontWeight: 'bold',
          fontSize: '10px'
        }
      },
      tickLength: 5,
      lineColor: '#637d8d'
    },
    yAxis: {
      gridLineColor: '#637d8d',
      gridLineDashStyle: 'dot',
      lineWidth: 1,
      lineColor: '#637d8d',
      labels: {
        style: {
          fontWeight: 'bold',
          fontSize: '10px'
        }
      },
      title: {
        style: {
          fontWeight: 'bold'
        }
      }
    }
  });

  return highcharts;
};
export const highchartsProvider: FactoryProvider = {
  provide: HIGHCHARTS,
  useFactory: highchartsFactory
};

