export const SCROLL_CONTAINER = new InjectionToken('scroll-container');
const scrollContainerFactory = (doc: Document) => {
  return doc.getElementById('content-wrapper');
};
export const scrollContainerProvider: FactoryProvider = {
  provide: SCROLL_CONTAINER,
  useFactory: scrollContainerFactory,
  deps: [DOCUMENT]
};


const myScriptFactory = () => window['MyScript'];
export const MYSCRIPT = new InjectionToken('myscript ', {
  factory: myScriptFactory,
  providedIn: 'root'
});


const jqueryFactory = () => window['jQuery'];
export const JQUERY = new InjectionToken('jquery ', {
  factory: jqueryFactory,
  providedIn: 'root'
});


const fabricJsFactory = () => window['fabric'];
export const FACBRIC_JS = new InjectionToken('fabric js', {
  providedIn: 'root',
  factory: fabricJsFactory
});
