import { Injectable, Inject } from '@angular/core';
import { ApiService } from '@core/services/api.service';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';

@Injectable({
  providedIn: 'root'
})
export class RegisterationBillingService {
  private readonly MODULE_NAME = 'OPD';

  constructor(
    @Inject(URL_CONSTANTS_TOKEN) private urlConstans: UrlConstants,
    private apiService: ApiService
  ) {}

  serviceUnitPrice<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstans.OPD.GET_SERVICE_UNIT_PRICE,
      payload
    );
  }

  servicesList<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstans.OPD.LIST_OF_AMOUNT_DEFINED_SERVICES_PROVIDED,
      payload
    );
  }
}
