import {
  Component,
  Renderer2,
  Inject,
  OnInit,
  ElementRef
} from '@angular/core';

import { DOCUMENT } from '@angular/common';
import { Store, select } from '@ngrx/store';
import { Observable } from 'rxjs';
import { getCurrentUnpinnedItem, getSideNavCollapsedState, AppState } from '@app/reducers';
import { slideLeftAnimation } from '@core/animations';
import { UserService, WINDOW, CLONEDEEP, LoginApiService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { AppConstant } from '@core/models/app-constant';
import { APP_CONSTANTS } from '@app/app.constant';
import { ToggleSideNav, PinnedScreens } from '@core/actions/layout-actions';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

import { finalize } from 'rxjs/operators';
import { IEmployeeMappedScreenResponse } from '@core/models/employe-mapped-screen-reponse';
import { SIDENAV_CONFIG_TOKEN, sidenavConfigProvider, ISidenavConfigs } from './sidenav-configs';

@Component({
  selector: 'app-sidebar',
  templateUrl: './side-nav.component.html',
  styleUrls: ['./side-nav.component.scss'],
  animations: [slideLeftAnimation],
  providers: [sidenavConfigProvider]
})
export class SideNavBarComponent implements OnInit {

  readonly TOGGLER_BTN_ID;
  sideNavCollapsed = false;
  public sideNavToggleState$: Observable<boolean>;
  public currentUnPinnedItem$: Observable<number>;

  public sideMenu = [];
  public userdata: UserSession;

  constructor(
    private renderer: Renderer2,
    private elRef: ElementRef,
    private router: Router,
    private toastr: ToastrService,
    private userService: UserService,
    private store: Store<AppState>,
    private loginService: LoginApiService,
    @Inject(CLONEDEEP) private cloneDeep,
    @Inject(DOCUMENT) private document: Document,
    @Inject(WINDOW) private window: Window,
    @Inject(APP_CONSTANTS) private appConstants: AppConstant,
    @Inject(SIDENAV_CONFIG_TOKEN) private sidenavConfig: ISidenavConfigs
  ) {
    this.TOGGLER_BTN_ID = this.sidenavConfig.TOGGLER_BTN_ID;
    this.userdata = this.userService.getUser();
    this.sideNavToggleState$ = this.store.pipe(select(getSideNavCollapsedState));
    this.currentUnPinnedItem$ = this.store.pipe(select(getCurrentUnpinnedItem));
  }

  doNavigate(screenLink: string) {
    this.router.navigateByUrl(screenLink);
    this.store.dispatch(new ToggleSideNav(!this.sideNavCollapsed));
  }

  fetchEmpMappedScreens() {
    if (this.userdata) {
    const payload = {
      orgId: this.userdata.orgId,
      regId: this.userdata.regId
    };
    this.loginService
      .fetchEmpMappedScreens<IEmployeeMappedScreenResponse>(payload)
      .subscribe(response => {
        if (response.responseCode === 'E200') {
          this.sideMenu = response.data
            .filter(moduleObj => moduleObj.modVersion === this.sidenavConfig.MOD_VERSION)
            .reduce((acc, val) => {
              const screenObj = {
                name: val.screenName,
                screenId: val.screenId,
                screenLink: this.appConstants.screenLinks[val.screenId]
              };
              const value = acc.find(moduleObj => {
                return val.moduleName === moduleObj.moduleName;
              });
              if (value) {
                value.screens.push(screenObj);
              } else {
                acc.push({
                  moduleName: val.moduleName,
                  menuIcon: this.appConstants.menuIcons[val.moduleName],
                  screens: [screenObj]
                });
              }
              return acc;
            }, []);
          this.fetchPinnedScreens();
        }
      });
    }
  }

  isClickedOnToggler(event) {
    let maxDepth = 2, targetParent: Element, parentContainer;
    const toggler: Element = this.document.getElementById(this.TOGGLER_BTN_ID);
    const { target } = event;
    do {
      --maxDepth;
      targetParent = target.parentElement;
      if (targetParent && targetParent.id === this.TOGGLER_BTN_ID) {
        parentContainer = targetParent;
      }
    } while (targetParent && maxDepth);
    return target.isEqualNode(toggler) || parentContainer;
  }

  attachKeyDownEventListenerOnWindow() {
    this.renderer.listen(this.window, 'keydown', (event) => {
      if (event.ctrlKey && (event.key === 'm' || event.key === 'M')) {
        event.preventDefault();
        this.sideNavCollapsed = !this.sideNavCollapsed;
        this.store.dispatch(new ToggleSideNav(this.sideNavCollapsed));
      }
    });
  }

  attachClickEventListenerOnWindow() {
    this.renderer.listen(this.window, 'click', event => {
      const sidenavElement = this.elRef.nativeElement;
      let targetElement = event.target;
      if (this.isClickedOnToggler(event)) { return; }
      do {
        if (targetElement === sidenavElement) { return; }
        targetElement = targetElement.parentNode;
      } while (targetElement);
      if (!this.sideNavCollapsed) {
        this.sideNavCollapsed = true;
        this.store.dispatch(new ToggleSideNav(this.sideNavCollapsed));
      }
    });
  }

  ngOnInit() {
    this.fetchEmpMappedScreens();
    this.currentUnPinnedItem$.subscribe((unPinnedItemId) => {
      let mdIndx = 0,
        scrIndx = 0,
        mods, scrs;
      for (mdIndx = 0, mods = this.sideMenu.length; mdIndx < mods; mdIndx++) {
        for (scrIndx = 0, scrs = this.sideMenu[mdIndx].screens.length; scrIndx < scrs; scrIndx++) {
          if (this.sideMenu[mdIndx].screens[scrIndx].screenId === unPinnedItemId) {
            this.sideMenu[mdIndx].screens[scrIndx].pinned = false;
            return;
          }
        }
      }
    });
    this.sideNavToggleState$.subscribe((collapsed) => { this.sideNavCollapsed = collapsed; });
    this.attachClickEventListenerOnWindow();
    this.attachKeyDownEventListenerOnWindow();
  }

  toggleScreenPin(selectedScreen) {
    const payload = {
      orgId: this.userdata.orgId,
      createdUsrId: this.userdata.regId,
      empId: this.userdata.regId,
      orgGroupId: this.userdata.orgGroupId,
      screenId: selectedScreen.screenId,
      insert: !selectedScreen.pinned
    };
    const errMsg = this.toastr.error.bind(this.toastr, 'Unable to pin menu item please try again after sometime');
    this.loginService.insertOrUpdateEmployeeFavouriteScrean(payload)
      .pipe(
        finalize(() => {
          this.store.dispatch(new PinnedScreens(this.getPinnedScreens(this.sideMenu)));
        })
      )
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          selectedScreen.pinned = !selectedScreen.pinned;
        } else if (response.responseCode === 'E400') { errMsg(); }
      }, () => errMsg());
  }

  fetchPinnedScreens() {
    const payload = {
      orgId: this.userdata.orgId,
      empId: this.userdata.regId
    };
    this.loginService.fetchEmployeeFavouriteScreen<any>(payload)
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          let mdIndx = 0,
            scrIndx = 0,
            mods, scrs, favIndx = 0, favs;

          for (favIndx = 0, favs = response.data.length; favIndx < favs; favIndx++) {
            for (mdIndx = 0, mods = this.sideMenu.length; mdIndx < mods; mdIndx++) {
              for (scrIndx = 0, scrs = this.sideMenu[mdIndx].screens.length; scrIndx < scrs; scrIndx++) {
                if (this.sideMenu[mdIndx].screens[scrIndx].screenId === response.data[favIndx].screenId) {
                  this.sideMenu[mdIndx].screens[scrIndx].pinned = response.data[favIndx].pinned;
                  break;
                }
              }
            }
          }
          this.store.dispatch(new PinnedScreens(this.getPinnedScreens(this.sideMenu)));
        }
      });
  }

  getPinnedScreens(menuList) {
    const _menuList = this.cloneDeep(menuList);
    return _menuList
      .reduce((acc, menu) => [...acc, ...menu.screens], [])
      .filter((screen) => screen.pinned);
  }

}
