import { Injectable, Inject } from '@angular/core';
import { ApiService } from '@core/services/api.service';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';

@Injectable({
  providedIn: 'root'
})
export class LoginApiService {
  readonly MODULE_NAME = 'LOGIN';

  constructor(
    @Inject(URL_CONSTANTS_TOKEN) private urlConstants: UrlConstants,
    private apiService: ApiService
  ) { }

  fetchListOfCommonValues<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.LOGIN.FETCH_COMMON_VALUES,
      payload
    );
  }

  fetchListOfSpecialities<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.LOGIN.FETCH_DOCTOR_SPECIALITIES,
      payload
    );
  }

  fetchUserDetails<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.NEW_LOGIN_URLS.FETCH_USER_DETAILS,
      payload
    );
  }

  checkBlurScreenOtpAuthentication<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.NEW_LOGIN_URLS.CHECK_BLUR_SCREEN_OTP_AUTHENTICATION,
      payload
    );
  }

  getOtp<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.NEW_LOGIN_URLS.GET_OTP,
      payload
    );
  }

  checkBlurScreenPswAuthentication<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.NEW_LOGIN_URLS.CHECK_BLUR_SCREAN_PSW_AUTHENTICATION,
      payload
    );
  }

  saveFailureLoginAttemptInBlureScrean<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.NEW_LOGIN_URLS.SAVE_FAILURE_LOG_IN_ATTEMPT_IN_BLURE_SCREAN,
      payload
    );
  }

  deleteFailureLoginAttemptInBlurScreen<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.NEW_LOGIN_URLS.DELETE_FAILURE_LOG_IN_ATTEMPT_IN_BLURE_SCREAN,
      payload
    );
  }

  logout<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.NEW_LOGIN_URLS.LOG_OUT,
      payload
    );
  }

  fetchEmpMappedScreens<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.NEW_LOGIN_URLS.FETCH_EMP_MAPPED_SCREENS,
      payload
    );
  }

  insertOrUpdateEmployeeFavouriteScrean<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.NEW_LOGIN_URLS.INSERT_OR_UPDATE_EMPLOYEE_FAVOURITE_SCREAN,
      payload
    );
  }

  fetchEmployeeFavouriteScreen<T>(payload) {
    return this.apiService.post<T>(
      this.MODULE_NAME,
      this.urlConstants.NEW_LOGIN_URLS.FETCH_EMPLOYEE_FAVOURITE_SCREEN,
      payload
    );
  }


}
