import { Injectable, Inject } from '@angular/core';
import { URL_CONSTANTS_TOKEN } from '@core/url.constant';
import { UrlConstants } from '@core/models/url-constants';
import { ApiService } from './api.service';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ZuulService {
  readonly MODULE_NAME = 'ZUUL';

  constructor(
    @Inject(URL_CONSTANTS_TOKEN) private urlConstants: UrlConstants,
    private apiService: ApiService
  ) {}

  pinger(createdUserId:  string) {
    return this.apiService.post(
      this.MODULE_NAME,
      this.urlConstants.ZUUL.SESSION_CHECK,
      null,
      {
        params: {
          regId: createdUserId
        }
      }
    );
  }
}
